import React from "react";
import {
	CashStack,
	CreditCard2Front,
	Diagram3,
	CashCoin,
	PeopleFill,
	PersonBadge,
} from "react-bootstrap-icons";
import { Dropdown } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
