import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import AdminDash from "../Admin/AdminDash";
import { useNavigate } from "react-router-dom";
import ClientAccounts from "../Client/ClientAccounts";
import {
	Bell,
	Calculator,
	Envelope,
	People,
	TicketDetailed,
} from "react-bootstrap-icons";
import { FetchLiveSubs } from "../../Network/SubApi";
import { VerifySub } from "../../Utils/VerifySub";
import { Dropdown } from "react-bootstrap";
const HomeDash = () => {
	//redux dispatch
	const dispatch = useDispatch();
	const navigate = useNavigate();

	useEffect(() => {
		FetchLiveSubs(User.token, ActiveCred.instLinker, 0, dispatch);
	}, []);

	const User = useSelector((state) => state.auth.user);
	const ActiveCred = useSelector((state) => state.cred.activeCred);
	const inst = useSelector((state) => state.inst.activeInst);
	const subs = useSelector((state) => state.sub.subs);
	const Sub = VerifySub(subs, ActiveCred.instLinker);

	return Sub > 0 ? (
		<div className="row m-3 justify-content-center">
			<div className="col-6 col-md-4 d-flex align-items-stretch">
				{parseInt(ActiveCred.admin) === 1 && inst.sub === "Kenya" ? (
					<div
						className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
						style={{ zIndex: 2000 }}
					>
						<Dropdown style={{ width: "100%" }} className="no-caret">
							<Dropdown.Toggle
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
								variant="transparent"
							>
								<div>
									<div className="d-flex justify-content-center">
										<Bell className="dash-icon" />
									</div>
									<p className="text-center text-wrap dash-text  ">
										Notifications
									</p>
								</div>
							</Dropdown.Toggle>
							<Dropdown.Menu>
								<Dropdown.Item>
									<p
										className="btn-link"
										onClick={() => {
											window.scroll(0, 0);
											navigate(`notifications`);
										}}
									>
										General Notifications
									</p>
								</Dropdown.Item>

								<Dropdown.Item>
									<p
										className="btn-link"
										onClick={() => {
											window.scroll(0, 0);
											navigate(`bulk-sms`);
										}}
									>
										SMS Notifications
									</p>
								</Dropdown.Item>
							</Dropdown.Menu>
						</Dropdown>
					</div>
				) : (
					<div
						className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
						onClick={() => {
							window.scroll(0, 0);
							navigate(`notifications`);
						}}
					>
						<Dropdown style={{ width: "100%" }} className="no-caret">
							<Dropdown.Toggle
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
								variant="transparent"
							>
								<div>
									<div className="d-flex justify-content-center">
										<Bell className="dash-icon" />
									</div>
									<p className="text-center text-wrap dash-text  ">
										Notifications
									</p>
								</div>
							</Dropdown.Toggle>
						</Dropdown>
					</div>
				)}
			</div>
			{parseInt(ActiveCred.admin) === 1 ? <AdminDash></AdminDash> : null}
			{parseInt(ActiveCred.admin) === 1 || parseInt(ActiveCred.staff) === 1 ? (
				<>
					<div className="col-6 col-md-4 d-flex align-items-stretch">
						<div
							className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`clients`);
							}}
						>
							<Dropdown style={{ width: "100%" }} className="no-caret">
								<Dropdown.Toggle
									style={{
										width: "100%",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
									}}
									variant="transparent"
								>
									<div>
										<div className="d-flex justify-content-center">
											<People className="dash-icon" />
										</div>
										<p className="text-center text-wrap dash-text  ">Clients</p>
									</div>
								</Dropdown.Toggle>
							</Dropdown>
						</div>
					</div>{" "}
					<div className="col-6 col-md-4 d-flex align-items-stretch">
						<div
							className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`entries`);
							}}
						>
							<Dropdown style={{ width: "100%" }} className="no-caret">
								<Dropdown.Toggle
									style={{
										width: "100%",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
									}}
									variant="transparent"
								>
									<div>
										<div className="d-flex justify-content-center">
											<TicketDetailed className="dash-icon" />
										</div>
										<p className="text-center text-wrap dash-text  ">Entries</p>
									</div>
								</Dropdown.Toggle>
							</Dropdown>
						</div>
					</div>{" "}
					<div className="col-6 col-md-4 d-flex align-items-stretch">
						<div
							className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`applications`);
							}}
						>
							<Dropdown style={{ width: "100%" }} className="no-caret">
								<Dropdown.Toggle
									style={{
										width: "100%",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
									}}
									variant="transparent"
								>
									<div>
										<div className="d-flex justify-content-center">
											<Envelope className="dash-icon" />
										</div>
										<p className="text-center text-wrap dash-text  ">
											Applications
										</p>
									</div>
								</Dropdown.Toggle>
							</Dropdown>
						</div>
					</div>{" "}
					<div className="col-6 col-md-4 d-flex align-items-stretch">
						<div
							className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`calculator`);
							}}
						>
							<Dropdown style={{ width: "100%" }} className="no-caret">
								<Dropdown.Toggle
									style={{
										width: "100%",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
									}}
									variant="transparent"
								>
									<div>
										<div className="d-flex justify-content-center">
											<Calculator className="dash-icon" />
										</div>
										<p className="text-center text-wrap dash-text  ">
											Loan Calculator
										</p>
									</div>
								</Dropdown.Toggle>
							</Dropdown>
						</div>
					</div>{" "}
				</>
			) : null}
			{parseInt(ActiveCred.client) === 1 ? (
				<>
					{" "}
					<div className="col-6 col-md-4 d-flex align-items-stretch">
						<div
							className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`client/applications`);
							}}
						>
							<Dropdown style={{ width: "100%" }} className="no-caret">
								<Dropdown.Toggle
									style={{
										width: "100%",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
									}}
									variant="transparent"
								>
									<div>
										<div className="d-flex justify-content-center">
											<Envelope className="dash-icon" />
										</div>
										<p className="text-center text-wrap dash-text  ">
											Applications
										</p>
									</div>
								</Dropdown.Toggle>
							</Dropdown>
						</div>
					</div>{" "}
					<ClientAccounts></ClientAccounts>
				</>
			) : null}
		</div>
	) : (
		<></>
	);
};

export default HomeDash;
