import React, { useEffect } from "react";
import { FetchClientEntries } from "../../Network/ClientApi";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

const ClientAccounts = () => {
	useEffect(() => {
		FetchClientEntries(User.token, ActiveCred.instLinker, dispatch, online);
	}, []);

	//redux dispatch
	const dispatch = useDispatch();
	const Navigate = useNavigate();
	const inst = useSelector((state) => state.inst.activeInst);
	const User = useSelector((state) => state.auth.user);
	const online = useSelector((state) => state.online.online);
	const ActiveCred = useSelector((state) => state.cred.activeCred);
	const Accounts = useSelector((state) => state.account.accounts).filter(
		(account) => parseInt(account.deleted) === 0
	);
	const Entries = useSelector((state) => state.entry.entrys).filter(
		(entry) =>
			parseInt(entry.deleted) === 0 &&
			parseInt(entry.clientLinker) === parseInt(ActiveCred.linker)
	);

	const FindAccount = (linker) =>
		Accounts.find((account) => parseInt(account.linker) === parseInt(linker));

	return (
		<div>
			<div className="row d-flex justify-content-center">
				<div className="col-12 col-md-8 d-flex justify-content-center">
					<table className="table table-sm table-responsive" id>
						<thead>
							<tr>
								<th>Account</th>{" "}
								{inst.sub === "Kenya" ? (
									<th>
										Code <br />{" "}
										<span className="text-center">
											<em>
												<small>{`{Mpesa Code For ACC Paybill Payment}`}</small>
											</em>
										</span>
									</th>
								) : null}
								<th>Bal</th>
								<th></th>
							</tr>
						</thead>
						<tbody>
							{[...new Set(Entries.map((entry) => entry.accountLinker))].map(
								(account) => (
									<tr key={account}>
										<td>{FindAccount(account)?.name}</td>
										{inst.sub === "Kenya" ? (
											<td>
												{ActiveCred.clientReg +
													FindAccount(account)
														?.name?.substring(0, 2)
														.toUpperCase()}
											</td>
										) : null}
										<td>
											{Entries.filter(
												(entry) =>
													parseInt(entry.accountLinker) === parseInt(account) &&
													(entry.type === "issued-loan" ||
														entry.type == "loan-interest" ||
														entry.type === "saving" ||
														entry.type == "savings-interest")
											).reduce((a, b) => +a + +b.amount, 0) -
												Entries.filter(
													(entry) =>
														parseInt(entry.accountLinker) ===
															parseInt(account) &&
														(entry.type === "payment" ||
															entry.type === "withdrawal" ||
															entry.type == "fine")
												).reduce((a, b) => +a + +b.amount, 0)}
										</td>
										<td>
											<span
												className="btn-link text-info"
												style={{ cursor: "pointer" }}
												onClick={() => Navigate(`client/entries/${account}`)}
											>
												<em>statement</em>
											</span>
										</td>
									</tr>
								)
							)}
						</tbody>
					</table>
				</div>
			</div>
		</div>
	);
};

export default ClientAccounts;
